a {
    cursor: pointer;

    &:hover {
        opacity: 0.8;
    }
}

.btn-rect {
    padding: 1.4rem 4.6rem;
    min-width: 24rem;
    height: 6rem;
    background-color: $deepblue_color;
    color: #FFF;
    border: 1px solid $deepblue_color;
    font-weight: 500;
    margin: 5.2rem auto 0 auto;

    &:hover {
        opacity: 1;
    }

    @media only screen and (max-width: 768px) {
        margin-top: 3.6rem auto 0 auto;
    }

    &:before {
        background-color: #FFF;
        border: unset;
    }

    &:after {
        border: unset;
    }

    &:hover {
        color: $deepblue_color;
    }
}

.underline-title {
    position: relative;
    width: 100%;
    display: flex;
    column-gap: 1.2rem;
    align-items: center;
    color: $orange_color;
    text-align: left !important;
    padding-bottom: 2.0rem;
    border-bottom: 1px solid #b0aeae;
    margin-bottom: 2.8rem;

    img {
        width: 4.8rem;
    }

    &:after {
        position: absolute;
        content: '';
        width: 16%;
        height: 3px;
        left: 0;
        bottom: -2px;
        background: $dodgerblue;
        transition: width 0.3s ease-in;
    }
}

.btn-top {
    display: none;
    position: fixed;
    width: 5.6rem;
    height: 5.6rem;
    bottom: 4.8rem;
    right: 4.8rem;
    z-index: 10000;

    .wrapper {
        position: relative;
        width: 100%;
        height: 100%;
        background: $dodgerblue;
        border-radius: 90%;

        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 2px 2px 4px #5d5d5dab, inset 2px 2px 4px #ececec6e;

        img {
            width: 3.6rem;
        }
    }
}

