.section-title {
    display: block;
    width: fit-content;
    position: relative;
    text-align: center;
    margin: 0 auto 6.4rem auto;

    @media only screen and (max-width: 767px) {
        margin-bottom: 4.0rem;
    }

    .en-title {
        color: $orange_color;
        margin-bottom: 1.6rem;

        @media only screen and (max-width: 767px) {
            margin-bottom: 1.2rem;
        }
    }

    .jp-title {
        color: $deepskyblue;
    }
}

section {
    display: block;
    position: relative;
    width: 100%;
    padding: 12.6rem 0;

    @media only screen and (max-width: 767px) {
        padding: 7.6rem 0;
    }
}

.business-list {
    display: grid;
    position: relative;
    width: 100%;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    column-gap: 4.8rem;
    row-gap: 4.8rem;

    @media only screen and (max-width: 767px) {
        column-gap: 4.8rem;
        row-gap: 4.8rem;
    }

    .business-item {
        display: block;
        position: relative;
        width: 100%;
        overflow: hidden;

        .thumb {
            display: block;
            position: relative;
            aspect-ratio: 395 / 264;
            object-fit: cover;
            width: 100%;
            height: auto;
            transition: all 0.3s ease-in-out;

            &:hover {
                scale: 1.3;
            }
        }

        .title {
            position: absolute;
            bottom: 1.2rem;
            left: 1.2rem;
            padding: 0.6rem 1.2rem;
            line-height: 100%;
            background: #ffffffb2;
        }
    }
}

.news-list {
    display: flex;
    flex-direction: column;
    row-gap: 1.2rem;

    .news-item {
        position: relative;
        width: 100%;
        display: flex;
        align-items: center;
        column-gap: 1.2rem;

        .date {
            width: 12rem;
        }
    }
}

.contact-banner {
    display: flex;
    position: relative;
    width: 100%;
    height: fit-content;
    background-color: $dodgerblue;

    .left-part,
    .right-part {
        position: relative;
        width: 50%;
    }

    .left-part {
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            opacity: 0.4;
        }
    }

    .right-part {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        color: $white_color;
        padding: 10.6rem 0;

        @media only screen and (max-width: 768px) {
            padding: 7.6rem 0;
        }
    }

    .btn-phone {
        display: flex;
        align-items: center;
        column-gap: 1.2rem;

        img {
            width: 5.2rem;
            margin-top: 1.2rem;
        }
    }

    .btn-rect {
        min-width: 32rem;
        height: 7.2rem;
        background-color: #eea92a;
        color: #FFF;
        border: 1px solid #eea92a;
        border-radius: 0.6rem;
        column-gap: 1.2rem;
        margin-top: 4.0rem;

        img {
            width: 4.0rem;
            display: block;
            position: relative;
            z-index: 1;
        }

        &:before {
            background-color: #f7b946;
        }
    }
}

.terms-section {
    .wrapper {
        display: flex;
        align-items: center;
        column-gap: 2.4rem;
        padding: 3.6rem 4.8rem;
        border: 1px solid $dodgerblue;
        margin: 0 auto;
        transition: all 0.1s ease-in-out;

        &:hover {
            border: 6px solid $dodgerblue;
        }

        .img-wrapper {
            position: relative;
            width: 38%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-shrink: 0;

            .section-img {
                position: relative;
                display: block;
                width: 80%;
            }
        }

        .content {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;

            .btn-terms {
                padding: 2.0rem 5.6rem;
                line-height: 4.4rem;
                text-align: center;
                color: $orange_color;
                border: 1px solid $orange_color;
                transition: all 0.3s ease-in-out;

                @media only screen and (max-width: 768px) {
                    padding: 1.6rem 4.6rem;
                    line-height: 3.4rem;
                }
            }
        }
    }
}

//vertical movement animation------------>
.scroller {
    position: relative;
    max-width: 100%;
    z-index: 1;

    &__inner {
        padding-block: 1rem;
        display: flex;
        flex-wrap: wrap;
        gap: 1rem;
    }

    &[data-animated="true"] {
        overflow: hidden;
        -webkit-mask: linear-gradient(90deg,
                transparent,
                white 6%,
                white 94%,
                transparent);
        mask: linear-gradient(90deg, transparent, white 6%, white 94%, transparent);

        .scroller__inner {
            width: max-content;
            flex-wrap: nowrap;
            animation: scroll var(--_animation-duration, 40s) var(--_animation-direction, forwards) linear infinite;
        }
    }

    &[data-direction="right"] {
        --_animation-direction: reverse;
    }

    &[data-direction="left"] {
        --_animation-direction: forwards;
    }

    &[data-speed="fast"] {
        --_animation-duration: 20s;
    }

    &[data-speed="slow"] {
        --_animation-duration: 60s;
    }
}

@keyframes scroll {
    to {
        transform: translate(calc(-50% - 0.5rem));
    }
}

